// accordion

const accordionItems = document.querySelectorAll('.accordion__item');
		
if(accordionItems.length > 0){
  
  for(let i=0; i < accordionItems.length; i++){
    accordionItems[i].addEventListener('click', function(){
      
      this.classList.toggle('active')
    })
  }
}

// anim
 // animations
   //1.Добавить класс _anim-items для необходимого элемента
   //2.Прописать стили для класса _active который подставит скрипт
   //3.Класс _anim-no-hide отменяет удаление класса _active и анимация не повторяется при обратном скроле
   const animItems = document.querySelectorAll('._anim-items');

   if (animItems.length > 0) {
      window.addEventListener('scroll', animOnScroll);
      function animOnScroll() {
         for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
               animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
               animItem.classList.add('_active');
            }
            else {
               if (!animItem.classList.contains('_anim-no-hide')) {
                  animItem.classList.remove('_active');
               }
            }
         }
      }
      function offset(el) {
         const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
         return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
      }
      setTimeout(() => {
         animOnScroll();
      }, 300);

   }
   
// slider
var sliderSelector = '.hero__slider',
    isMove = false,
    options = {
      init: false,
      loop: true,
      speed:800,
      autoplay:{
        delay:3000
      },
      effect: 'cube', // 'cube', 'fade', 'coverflow',
      cubeEffect: {
        shadow: true,
        slideShadows: true,
        shadowOffset: 40,
        shadowScale: 0.94,
      },
      grabCursor: true,
      pagination: {
        el: '.swiper-pagination',
        clickable: true
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      },
      // Events
      on: {
        init: function(){
          this.autoplay.stop();
        },
        imagesReady: function(){
          this.el.classList.remove('loading');
          this.autoplay.start();
        },
        touchMove: function(event){
          if (!isMove) {
            this.el.classList.remove('scale-in');
            this.el.classList.add('scale-out');
            isMove = true;
          }
        },
        touchEnd: function(event){
          this.el.classList.remove('scale-out');
          this.el.classList.add('scale-in');
          setTimeout(function(){
            isMove = false;
          }, 300);
        },
        slideChangeTransitionStart: function(){
          console.log('slideChangeTransitionStart '+this.activeIndex);
          if (!isMove) {
            this.el.classList.remove('scale-in');
            this.el.classList.add('scale-out');
          }
        },
        slideChangeTransitionEnd: function(){
          console.log('slideChangeTransitionEnd '+this.activeIndex);
          if (!isMove) {
            this.el.classList.remove('scale-out');
            this.el.classList.add('scale-in');
          }
        },
        transitionStart: function(){
          console.log('transitionStart '+this.activeIndex);
        },
        transitionEnd: function(){
          console.log('transitionEnd '+this.activeIndex);
        },
        slideChange:function(){
          console.log('slideChange '+this.activeIndex);
          console.log(this);
        }
      }
    },
    mySwiper = new Swiper(sliderSelector, options);

// Initialize slider
mySwiper.init();


// team

const teamBtns = document.querySelectorAll('.team__nav-item');
const teamItems = document.querySelectorAll('.team__item');

teamBtns.forEach(function(btn){
  btn.addEventListener('click', function(){

    teamBtns.forEach(function(btn){
      btn.classList.remove('active');
    })

    this.classList.add('active');

    let btnName = this.dataset.name;

    for(let i = 0; i<teamItems.length;i++){
      let itemName = teamItems[i].dataset.name;

      if(btnName === itemName){
        teamItems[i].classList.add('show');
      }
      else{
        teamItems[i].classList.remove('show');
      }
    }

  })
})



